					<el-input v-model="ruleForm.shiyanshimingcheng" placeholder="实验室名称" clearable  :readonly="ro.shiyanshimingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="实验室名称" prop="shiyanshimingcheng" >
