				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="实验室分类" prop="shiyanshifenlei" >
					<el-input v-model="ruleForm.shiyanshifenlei" placeholder="实验室分类" clearable  :readonly="ro.shiyanshifenlei"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="实验室分类" prop="shiyanshifenlei" >
					<el-input v-model="ruleForm.shiyanshifenlei" placeholder="实验室分类" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="实验室规模" prop="shiyanshiguimo" >
					<el-input v-model="ruleForm.shiyanshiguimo" placeholder="实验室规模" clearable  :readonly="ro.shiyanshiguimo"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="实验室规模" prop="shiyanshiguimo" >
					<el-input v-model="ruleForm.shiyanshiguimo" placeholder="实验室规模" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.shiyanshitupian" label="实验室图片" prop="shiyanshitupian" >
					<file-upload
						tip="点击上传实验室图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.shiyanshitupian?ruleForm.shiyanshitupian:''"
						@change="shiyanshitupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.shiyanshitupian" label="实验室图片" prop="shiyanshitupian" >
					<img v-if="ruleForm.shiyanshitupian.substring(0,4)=='http'&&ruleForm.shiyanshitupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.shiyanshitupian" width="100" height="100">
					<img v-else-if="ruleForm.shiyanshitupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.shiyanshitupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.shiyanshitupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="用户账号" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="用户账号" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="用户姓名" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="用户姓名" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="用户类型" prop="yonghuleixing" >
					<el-input v-model="ruleForm.yonghuleixing" placeholder="用户类型" clearable  :readonly="ro.yonghuleixing"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="用户类型" prop="yonghuleixing" >
					<el-input v-model="ruleForm.yonghuleixing" placeholder="用户类型" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="预约时间" prop="yuyueshijian" >
					<el-date-picker
						format="yyyy 年 MM 月 dd 日"
						value-format="yyyy-MM-dd"
						v-model="ruleForm.yuyueshijian" 
						type="date"
						:readonly="ro.yuyueshijian"
						placeholder="预约时间"
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.yuyueshijian" label="预约时间" prop="yuyueshijian" >
					<el-input v-model="ruleForm.yuyueshijian" placeholder="预约时间" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="textarea" v-if="type!='info'" label="预约备注" prop="yuyuebeizhu" >
				<el-input
					style="min-width: 200px; max-width: 600px;"
					type="textarea"
					:rows="8"
					placeholder="预约备注"
					v-model="ruleForm.yuyuebeizhu" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.yuyuebeizhu" label="预约备注" prop="yuyuebeizhu" >
				<span class="text">{{ruleForm.yuyuebeizhu}}</span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	export default {
		data() {
			return {
				id: '',
				type: '',
			
			
				ro:{
					shiyanshibianhao : false,
					shiyanshimingcheng : false,
					shiyanshifenlei : false,
					shiyanshiguimo : false,
					shiyanshitupian : false,
					yuyuebeizhu : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					yonghuleixing : false,
					yuyueshijian : false,
					sfsh : false,
					shhf : false,
				},
			
				ruleForm: {
					shiyanshibianhao: '',
					shiyanshimingcheng: '',
					shiyanshifenlei: '',
					shiyanshiguimo: '',
					shiyanshitupian: '',
					yuyuebeizhu: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					yonghuleixing: '',
					yuyueshijian: '',
					shhf: '',
				},

				rules: {
					shiyanshibianhao: [
					],
					shiyanshimingcheng: [
					],
					shiyanshifenlei: [
					],
					shiyanshiguimo: [
					],
					shiyanshitupian: [
					],
					yuyuebeizhu: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					yonghuleixing: [
					],
					yuyueshijian: [
					],
					sfsh: [
					],
					shhf: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.yuyueshijian = this.getCurDate()
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='shiyanshibianhao'){
							this.ruleForm.shiyanshibianhao = obj[o];
							this.ro.shiyanshibianhao = true;
							continue;
						}
						if(o=='shiyanshimingcheng'){
							this.ruleForm.shiyanshimingcheng = obj[o];
							this.ro.shiyanshimingcheng = true;
							continue;
						}
						if(o=='shiyanshifenlei'){
							this.ruleForm.shiyanshifenlei = obj[o];
							this.ro.shiyanshifenlei = true;
							continue;
						}
						if(o=='shiyanshiguimo'){
							this.ruleForm.shiyanshiguimo = obj[o];
							this.ro.shiyanshiguimo = true;
							continue;
						}
						if(o=='shiyanshitupian'){
							this.ruleForm.shiyanshitupian = obj[o];
							this.ro.shiyanshitupian = true;
							continue;
						}
						if(o=='yuyuebeizhu'){
							this.ruleForm.yuyuebeizhu = obj[o];
							this.ro.yuyuebeizhu = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='yonghuleixing'){
							this.ruleForm.yonghuleixing = obj[o];
							this.ro.yonghuleixing = true;
							continue;
						}
						if(o=='yuyueshijian'){
							this.ruleForm.yuyueshijian = obj[o];
							this.ro.yuyueshijian = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
							this.ro.yonghuzhanghao = true;
						}
						if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.yonghuxingming = json.yonghuxingming
							this.ro.yonghuxingming = true;
						}
						if(((json.yonghuleixing!=''&&json.yonghuleixing) || json.yonghuleixing==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.yonghuleixing = json.yonghuleixing
							this.ro.yonghuleixing = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `yuyueshiyanshi/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					if(this.ruleForm.shiyanshitupian!=null) {
						this.ruleForm.shiyanshitupian = this.ruleForm.shiyanshitupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `yuyueshiyanshi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.yuyueshiyanshiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.yuyueshiyanshiCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			shiyanshitupianUploadChange(fileUrls) {
				this.ruleForm.shiyanshitupian = fileUrls;
			},
		}
	};
</script>
